import React from 'react';
import Header from './Header'
import Inventory from './Inventory';
import sampleFishes from '../sample-fishes'
import Fish from './Fish';
import Order  from './Order';
import base from '../base'
class App extends React.Component{
    state ={
        fishes : {},
        order : {}
    };
    componentDidMount()
    {
        const {params}=this.props.match;
        //first reinsiate our localstorage
        const localStorageRef = localStorage.getItem(params.storeId);
        if(localStorageRef)
        {
            this.setState({order:JSON.parse(localStorageRef)})
        }
        this.ref = base.syncState(`${params.storeId}/fishes`,{
        context:this,
        state : 'fishes'
        });
    }
    componentDidUpdate()
    {
        localStorage.setItem(this.props.match.params.storeId,JSON.stringify(this.state.order));
    }
    componentWillUnmount()
    {
        base.removeBinding(this.ref)
    }
    addFish = fish =>{
        console.log("add");
        //1. take acopy of exsisting state
        const fishes = {...this.state.fishes};
        //2. add our new fish to fishes
        fishes[`fish${Date.now()}`] = fish;
        //3. set the new fishes object to state
        this.setState({
            fishes
        });
    };
    updateFish = (key,updateFish) =>{
        const fishes = {...this.state.fishes};
        fishes[key]=updateFish;
        this.setState({
            fishes
        });
    } 
    deleteFish = (key)=>{
        const fishes ={...this.state.fishes};
        fishes[key] =null;
        this.setState({
            fishes
        }); 
        
    }
    loadSampleFishes = ()=>{
       this.setState({fishes : sampleFishes})
    }
    addToOrder = (key)=>{
        // 1. take a copy of satate
        const order = {...this.state.order};
        // 2. either add or increment 
        order[key] = order[key]+1 || 1;
        // 3. set state to update
        this.setState({order});
    }
    removeFromOrder = key =>{
                // 1. take a copy of satate
                const order = {...this.state.order};
                // 2. remove that order  
                delete order[key];
                // 3. set state to update
                this.setState({order});
    }
    render()
    {
        return (
            <div className="catch-of-the-day">
                <div className="menu">
                    <Header tagline="69 resturant"/>
                    <ul className="fishes">
                    {Object.keys(this.state.fishes).map(key=> <Fish key={key} details ={this.state.fishes[key]} addToOrder={this.addToOrder} index={key}/>)}
                    </ul>
                </div>
                <Order 
                fishes={this.state.fishes} 
                order={this.state.order}
                removeFromOrder={this.removeFromOrder}
                />
                <Inventory 
                addFish = {this.addFish}  
                updateFish={this.updateFish}
                deleteFish ={this.deleteFish}
                loadSampleFishes={this.loadSampleFishes}
                fishes  = {this.state.fishes}
                />
            </div>
        );
    }
}
export default App;
